<template>
  <div class="bookDetails">
    <div class="top">
      <div class="back-btn" @click="$router.go(-1)">返回</div>
    </div>
    <div class="book-msg">
      <div class="left">
        <img :src="bookmsg.image" alt="">
      </div>
      <div class="right">
        <p class="book-name">{{bookmsg.title}}</p>
        <span class="author">作者：{{bookmsg.author}}</span>
        <span class="source">更新：{{bookmsg.update_time}}</span>
        <span class="update-time">采集源：{{bookmsg.source=="shenqi"?"追书神器":"追书网"}}</span>
        <span class="update-time">状态：{{bookmsg.flag==0?"连载中":"已完结"}}</span>
        <span class="update-time" style="width:100%;height:100%;word-break:break-all;word-wrap:break-word;overflow:hidden;">源链接：{{bookmsg.link}}</span>
        <p class="brief-introduction">
           {{bookmsg.intro}}
        </p>
      </div>
    </div>
    <div class="chapter-catalogue">章节目录</div>
    <div class="chapter-list">
      <ul >
        <el-checkbox-group v-model="checkList">
          <li class="item" v-for="(item,index) in chapters" :key="index">
            <div class="choose">
                <el-checkbox  :label="item.id" :key="item.id">{{""}}</el-checkbox>
            </div>
            <div class="num" @click="godetais(item.title,item.id)">{{item.id}}&#12288;{{item.title}}</div>
          </li>
         </el-checkbox-group>
         <li v-if="chapters.length==0">章节正在采集中，请稍后刷新</li>
      </ul>
    </div>
    <div class="btn-box">
      <div class="delete" @click="deletachapters">删除所选章节</div>
      <div class="to-update" @click="gengxing">{{caijistatus==0||caijistatus==3?"立即采集":caijistatus==4?"采集中":"更新"}}</div>
      <div class="again" @click="chongcai">重采</div>
      <div class="empty" @click="qingkongzhang" >清空所有章节</div>
      <div class="delete-book" @click="shanchu">删除此书</div>
      <div class="delete-book" @click="bian">{{status==3?"开启更新":"暂停更新"}}</div>
    </div>
    <div class="source">
      <div class="replace">
        <span>换源：</span>
        <el-select v-model="source" placeholder="请选择" size="mini">
          <el-option
            v-for="item in yuanlist"
            :key="item.value"
            :label="item.source=='shenqi'?'追书神器':'追书网'"
            :value="item.source">
          </el-option>
        </el-select>
      </div>
      <div class="link">
        <span>源链接：</span>
        <input type="text" placeholder="源链接" v-model="ylink">
      </div>
      <div class="btn" @click="huanyuan">确定</div>
    </div>
    <div class="insert">
      <div class="zhang">
        <span>插入章节：</span>
        <input type="text" placeholder="章节ID" v-model="cid">
        <input type="text" placeholder="采集链接" v-model="clink">
      </div>
      <div class="btn" @click="charuzhang">插入</div>
    </div>
  </div>
</template>
<script>
  import axios from 'axios'
  export default {
    data(){
      return{
        id:"",
        fanganid:"",
        bookmsg:"",
        yuanlist:[],
        source:"",
        chapters:[],//章节
        checkList:[],
        cid:"",
        clink:"",
        ylink:"",
        status:"",//当前书籍状态//更新不更新
        caijistatus:"",
     }
    },
    methods:{
      bian(){
        let s=0;
        //状态（0：停止 1：开启）
        if(this.status==3){//现在是停止更新状态，要显示开启更新，
          this.status=0;
          s=1;
          console.log("点击开启，传"+s)
        }else{
          this.status=3;
          this.caijistatus=3
          s=0;
          console.log("点击暂停，传"+s)
        }
        let token=sessionStorage.getItem("token");
                if(token==""||token==undefined){
          this.$router.push("/")
        }
        let url="http://zhuishu.zhiask.cn/api";
        let data={
          "action": "upd_status",
          "data": {
            "id": this.id,
            "status": s
          }
        }
        axios({
          method: 'post',
          url: url,
          data: JSON.stringify(data),
          headers:{
            "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
            "Access-Token":token
          },
          contentType: "text/html;charset=utf-8",
          dataType: "json",
        })
        .then(res=>{
          if(res.data.errcode==3){
            this.$message.error("登录过期，请重新登录")
            this.$router.push("/");
          }
          if(res.data.errcode==0){
            if(s==1){
              this.$message({
                message: '开启更新成功',
                type: 'success'
              });
              this.getdata();
            }else{
              this.$message({
                message: '暂停更新成功',
                type: 'success'
              });
              this.getdata();
            }
          }else{
            this.$message.error(res.data.errmsg);
          }
        })
      },
      godetais(title,cid){
        sessionStorage.setItem("bid",this.id);
        sessionStorage.setItem("cid",cid);
        sessionStorage.setItem("title",title);
        this.$router.push({path:"chapterDetails"})
      },
      getdata(){
        let token=sessionStorage.getItem("token");
              if(token==""||token==undefined){
        this.$router.push("/")
        }
        let url="http://zhuishu.zhiask.cn/api";
        let data={
          "action": "book_detail",
          "data": {
            "id": this.id,
          }
        }
        axios({
          method: 'post',
          url: url,
          data: JSON.stringify(data),
          headers:{
            "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
            "Access-Token":token
          },
          contentType: "text/html;charset=utf-8",
          dataType: "json",
        })
        .then(res=>{
        if(res.data.errcode==3){
          this.$message.error("登录过期，请重新登录")
          this.$router.push("/");
        }
          if(res.data.errcode==0){
            this.bookmsg=res.data.data;
            this.status=this.bookmsg.status;
            this.caijistatus=this.bookmsg.status;
            sessionStorage.setItem("clink",this.bookmsg.catalog_link);
            this.getyuan();
            this.getzhang();
          }else{
            this.$message.error(res.data.errmsg);
          }
        })
      },
      //获取书籍
      getzhang(){
        let token=sessionStorage.getItem("token");
        if(token==""||token==undefined){
          this.$router.push("/")
        }
        let url=`http://zhuishu.zhiask.cn/${this.bookmsg.catalog_link}`;
        axios({
          method: 'get',
          url: url,
          headers:{
            "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
            "Access-Token":token,
            'cache-control': 'no-cache', 
          },
          contentType: "text/html;charset=utf-8",
          dataType: "json",
        })
        .then(res=>{
         if(res.data.errcode==3){
          this.$message.error("登录过期，请重新登录")
          this.$router.push("/");
        }
          let arr=res.data.Chapters;
          let arr2=[];
          for(let a=arr.length;a>0;a--){
            for(let key in arr[a]){
              let obj1={
                "id":key,
                "title":arr[a][key]
              }
              arr2[arr2.length]=obj1;
            }
          }
          this.chapters=arr2;
        })
      },
      //删除选中的章节
      deletachapters(){
        this.$confirm('此操作将永久删除该章节, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
        }).then(() => {
          let url="http://zhuishu.zhiask.cn/api";
          for(let a=0;a<this.checkList.length;a++){
            this.checkList[a]=this.checkList[a]/1
            
          }
          console.log(this.checkList);
          let token=sessionStorage.getItem("token")
          axios({
            method: 'post',
            url: url,
            data: JSON.stringify({
              "action": "del_chapters",
              "data": {
                "bid":this.id,
                "idx":this.checkList
              }
            }),
            headers:{
              "Content-Type": "application/json" ,
            "Access-Token":token
            },
            contentType: "text/html;charset=utf-8",
            dataType: "json",
          })
          .then(res=>{
            if(res.data.errcode==3){
              this.$message.error("登录过期，请重新登录")
              this.$router.push("/");
            }
            console.log(res.data);
            if(res.data.errcode==0){
              this.$message({
                message: '删除成功',
                type: 'success'
              });
              this.getdata();
            }else{
              this.$message.error(res.data.errmsg);
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      },
      //获取源
      getyuan(){
        let url="http://zhuishu.zhiask.cn/api";
        let data={
          "action": "list_crawler",
            "data": {
            }
        }
        let token=sessionStorage.getItem("token")
        axios({
          method: 'post',
          url: url,
          data: JSON.stringify(data),
          headers:{
            "Content-Type": "application/json" ,
          "Access-Token":token
          },
          contentType: "text/html;charset=utf-8",
          dataType: "json",
        })
        .then(res=>{
          if(res.data.errcode==3){
            this.$message.error("登录过期，请重新登录")
            this.$router.push("/");
          }
          this.yuanlist=res.data.data;
          for(let a=0;a<this.yuanlist.length;a++){
            if(this.yuanlist[a].source==this.bookmsg.source){
              this.fanganid=this.yuanlist[a].id
            }
          }
        })
      },
      //更新
      gengxing(){
        let url="http://zhuishu.zhiask.cn/api";
        let data={
          "action": "upd_book",
            "data": {
              "id":this.id
            }
        }
        let token=sessionStorage.getItem("token")
        axios({
          method: 'post',
          url: url,
          data: JSON.stringify(data),
          headers:{
            "Content-Type": "application/json" ,
          "Access-Token":token
          },
          contentType: "text/html;charset=utf-8",
          dataType: "json",
        })
        .then(res=>{
          if(res.data.errcode==3){
            this.$message.error("登录过期，请重新登录")
            this.$router.push("/");
          }
          if(res.data.errcode==0){
            if(this.caijistatus==0||this.caijistatus==3){
              this.$message({
                message: '正在采集',
                type: 'success'
              });
              this.caijistatus=4;
              let routeData = this.$router.resolve({
                name: "/taskDetails",
                query: {bid:this.bookmsg.id,fanganid:this.fanganid,type:0,source:this.bookmsg.source},
              });
              window.open(routeData.href, '_blank');
              return;
            }else{
              this.$message({
                message: '更新成功',
                type: 'success'
              });
            }
            this.bookmsg=res.data.data;
          }else{
            this.$message.error(res.data.errmsg);
          }
        })
      },
      //重采
      chongcai(){
        this.$confirm('此操作将重采, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
        }).then(() => {
          for(let a=0;a<this.checkList.length;a++){
            this.checkList[a]=this.checkList[a]/1
          }
          let url="http://zhuishu.zhiask.cn/api";
          let data={
            "action": "re_crawler",
              "data": {
                "bid":this.id/1,
                "idx":this.checkList
              }
          }
          let token=sessionStorage.getItem("token")
          axios({
            method: 'post',
            url: url,
            data: JSON.stringify(data),
            headers:{
              "Content-Type": "application/json" ,
            "Access-Token":token
            },
            contentType: "text/html;charset=utf-8",
            dataType: "json",
          })
          .then(res=>{
          if(res.data.errcode==3){
            this.$message.error("登录过期，请重新登录")
            this.$router.push("/");
          }
            if(res.data.errcode==0){
              this.$message({
                message: '重采成功',
                type: 'success'
              });
            }else{
              this.$message.error(res.data.errmsg)
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消重采'
          });          
        });
      },
      
      //清空章节
      qingkongzhang(){
        this.$confirm('此操作将永久清空章节, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
        }).then(() => {
          let url="http://zhuishu.zhiask.cn/api";
          let data={
            "action": "cls_chapter",
              "data": {
                "id":parseInt(this.id)
              }
          }
          let token=sessionStorage.getItem("token")
          axios({
            method: 'post',
            url: url,
            data: JSON.stringify(data),
            headers:{
              "Content-Type": "application/json" ,
            "Access-Token":token
            },
            contentType: "text/html;charset=utf-8",
            dataType: "json",
          })
          .then(res=>{
            if(res.data.errcode==3){
              this.$message.error("登录过期，请重新登录")
              this.$router.push("/");
            }
            if(res.data.errcode==0){
              this.$message({
                message: '清空成功',
                type: 'success'
              });
            }else{
              this.$message.error(res.data.errmsg);
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消清空'
          });          
        });
      },
      //删除书籍
      shanchu(){
        this.$confirm('此操作将永久删除该书籍, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
        }).then(() => {
          let url="http://zhuishu.zhiask.cn/api";
          let data={
            "action": "del_book",
              "data": {
                "id":parseInt(this.id)
              }
          }
          let token=sessionStorage.getItem("token")
          axios({
            method: 'post',
            url: url,
            data: JSON.stringify(data),
            headers:{
              "Content-Type": "application/json" ,
            "Access-Token":token
            },
            contentType: "text/html;charset=utf-8",
            dataType: "json",
          })
          .then(res=>{
            if(res.data.errcode==3){
              this.$message.error("登录过期，请重新登录")
              this.$router.push("/");
            }
            if(res.data.errcode==0){
              this.$message({
                message: '删除成功',
                type: 'success'
              });
            }else{
              this.$message.error(res.data.errmsg);
            }
          })
          .then(res=>{
            if(res.data.errcode==3){
              this.$router.push("/")
            }
            console.log(res.data.data);
            if(res.data.errcode==0){
              this.$message({
                message: '正在删除中',
                type: 'success'
              });
              this.set();
              if(this.list.length!=res.data.data.length){
                console.log(132)
                this.loading=false;
                clearInterval(this.t)
              }
            }else{
              this.$message.error(res.data.errmsg);
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      },
      //插入章节
      charuzhang(){
        let url="http://zhuishu.zhiask.cn/api";
        let data={
          "action": "insert_chapter",
            "data": {
              "bid":parseInt(this.id),
              "cid": parseInt(this.cid) ,
              "link":this.clink
            }
        }
        let token=sessionStorage.getItem("token")
        axios({
          method: 'post',
          url: url,
          data: JSON.stringify(data),
          headers:{
            "Content-Type": "application/json" ,
          "Access-Token":token
          },
          contentType: "text/html;charset=utf-8",
          dataType: "json",
        })
        .then(res=>{
              if(res.data.errcode==3){
                this.$message.error("登录过期，请重新登录")
                this.$router.push("/");
              }
                if(res.data.errcode==0){
                  this.$message({
                    message: '添加成功',
                    type: 'success'
                  });
                }else{
                  this.$message.error(res.data.errmsg);
                }
              })
            },
      //换源
      huanyuan(){
        let url="http://zhuishu.zhiask.cn/api";
        let data={
          "action": "change_source",
            "data": {
              "id":parseInt(this.id),
              "source":this.source,
              "link":this.ylink
            }
        }
        let token=sessionStorage.getItem("token")
        axios({
          method: 'post',
          url: url,
          data: JSON.stringify(data),
          headers:{
            "Content-Type": "application/json" ,
          "Access-Token":token
          },
          contentType: "text/html;charset=utf-8",
          dataType: "json",
        })
        .then(res=>{
        if(res.data.errcode==3){
          this.$message.error("登录过期，请重新登录")
          this.$router.push("/");
        }
          if(res.data.errcode==0){
            this.$message({
              message: '换源成功',
              type: 'success'
            });
          }else{
            this.$message.error(res.data.errmsg);
          }
        })
      }
    },
    created(){
      this.id=this.$route.query.id;
      this.getdata();
      
    }
  }
</script>
<style>
  body{
  box-sizing: border-box;
}
a,div,img,span,p,input,h1,h2,h3,h4,h5,h6,header,body,footer,ul,li{
  margin:0px;
  padding:0px;
}
.bookDetails{
  box-sizing:border-box;
  padding-left:32px;
}
.bookDetails .top{
  width: 100%;
  padding: 14px 0px 15px 0px;
}
.bookDetails .top .back-btn{
  width: 80px;
  height: 30px;
  border-radius: 4px;
  color: rgba(16, 16, 16, 1);
  font-size: 14px;
  text-align: center;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  line-height: 30px;
  cursor: pointer;
}
.bookDetails .book-msg{
  display:flex;
}
.bookDetails .book-msg .left{
  width: 111px;
  height: 120px;
  background: #E8E8E8;
}
.bookDetails .book-msg .left img{
  width:100%;
  height: 100%;
}
.bookDetails .book-msg .right{
  width: 80%;
  margin-left:126px;
  color: rgba(16, 16, 16, 1);
  font-size: 14px;
  text-align: left;
  font-family: SourceHanSansSC-regular;
}
.bookDetails .book-msg .right .book-name{
  width: 180px;
  height: 29px;
  font-size: 20px;
  text-align: left;
  font-family: SourceHanSansSC-regular;
  margin-bottom: 10px;
}
.bookDetails .book-msg .right span{
  margin-right: 15px;
}
.bookDetails .book-msg .right .brief-introduction{
  margin-top:10px;
  line-height: 25px;
}
.chapter-catalogue{
  width: 1165px;
  height: 48px;
  line-height: 48px;
  border: 1px solid rgba(187, 187, 187, 1);
  margin-top:50px;
  padding-left:10px;
}
.chapter-list{
  width: 1162px;
  height: 230px;
  overflow-y: auto;
  margin-top:15px;
  padding-right:15px;
}
.chapter-list ul li{
  list-style: none;
  display:flex;
  font-size: 14px;
  font-family: SourceHanSansSC-regular;
  border-bottom: 1px solid #BBBBBB;
  padding:11px 0px;
}
.chapter-list ul li:first-child{
  padding-top:0px; 
}
.chapter-list ul li .choose{
  margin-right:5px;
}
.chapter-list ul li .num{
  margin-right: 14px;
  cursor: pointer;
}
.chapter-list ul li .item-order{
  margin-right: 30px;
}
.bookDetails .btn-box{
  display: flex;
  margin-top:20px;
}
.bookDetails .btn-box div{
  height: 32px;
  border-radius: 8px;
  color: rgba(16, 16, 16, 1);
  font-size: 14px;
  text-align: center;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  line-height: 32px;
  margin-right: 20px;
  padding:0px 15px;
  cursor: pointer;
}
.bookDetails>.source{
  margin-top:15px;
  display: flex;
  color: rgba(16, 16, 16, 1);
  font-size: 14px;
  font-family: Microsoft Yahei;
}
.bookDetails>.source>.replace{
  margin-right: 35px;
}
.bookDetails>.source select{
  width: 200px;
  height: 30px;
  outline: none;
  border: 1px solid rgba(187, 187, 187, 1);
  margin-left:10px;
  border-radius: 8px;
  -webkit-border-radius:8px;
  -moz-border-radius:8px;
  overflow: hidden;
  
}
.bookDetails>.source input{
  width: 166px;
  height: 30px;
  padding-left:8px;
  color: rgba(136, 136, 136, 1);
  font-size: 14px;
  text-align: left;
  border: 1px solid rgba(187, 187, 187, 1);
  margin-left: 10px;
  border-radius: 6px;
  outline: none;
}
.bookDetails>.source .btn{
  width: 60px;
  height: 30px;
  border-radius: 8px;
  text-align: center;
  border: 1px solid rgba(187, 187, 187, 1);
  line-height: 30px;
  margin-left:25px;
  cursor: pointer;
}
.bookDetails>.insert{
  margin-top:15px;
  display: flex;
  color: rgba(16, 16, 16, 1);
  font-size: 14px;
  font-family: Microsoft Yahei;
}
.bookDetails>.insert .zhang input{
  width: 166px;
  height: 30px;
  padding-left:8px;
  color: rgba(136, 136, 136, 1);
  font-size: 14px;
  text-align: left;
  border: 1px solid rgba(187, 187, 187, 1);
  margin-left: 10px;
  border-radius: 6px;
  outline: none;
}
.bookDetails>.insert .btn{
  width: 60px;
  height: 30px;
  border-radius: 8px;
  text-align: center;
  border: 1px solid rgba(187, 187, 187, 1);
  line-height: 30px;
  margin-left:25px;
  cursor: pointer;
}
</style>


